.slogan {
    position: absolute;
    width: 100%;
    height: 6rem;
    background: linear-gradient(90deg, #441d0b, #390d0c);
    margin: 0 auto;
    &__title {
        color: white;
        font-size: 1.6rem;
        text-align: center;
        margin-top: 2rem;
    }
}

.slogan-color {
    position: absolute;
    width: 100%;
    height: 7.5rem;
    background: linear-gradient(90deg, #c02d28, #e66125);
    margin: 0 auto;
    transform: rotate(-0.7deg);
}