.showMenu {
  animation: showMenu 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both; }

.hideMenu {
  animation: hideMenu 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both; }

.opacityShow {
  animation-name: opacityShow;
  animation-duration: 0.3s; }

.rotate-90-ccw {
  animation: rotate-90-ccw 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both; }

.rotate-anti-90 {
  animation: rotate-anti-90 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both; }

@keyframes showMenu {
  0% {
    transform: scaleY(0.4);
    transform-origin: 100% 0%; }
  100% {
    transform: scaleY(1);
    transform-origin: 100% 0%; } }

@keyframes hideMenu {
  0% {
    transform: scaleY(1);
    transform-origin: 100% 0%; }
  100% {
    transform: scaleY(0);
    transform-origin: 100% 0%; } }

@keyframes opacityShow {
  0% {
    opacity: 0; }
  70% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes rotate-90-ccw {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(-45deg) translate(2.5px, -2.5px); } }

@keyframes rotate-anti-90 {
  0% {
    transform: rotate(-45deg) translate(2.5px, -2.5px); }
  100% {
    transform: rotate(0); } }

