.carousel {
  position: relative;
  height: 17rem;
  width: auto;
  background-color: #161616;
  margin-top: 3rem;
  padding-top: 1rem;
  &__window {
    width: 1120px;
    overflow: hidden;
    margin: 0 auto;
  }
  &__item {
    @include link();
    flex-shrink: 0;
    display: block;
    width: 20rem;
    height: 10rem;
    border-radius: 5px;
    margin-right: 3rem;
  }
  &__nav {
    position: absolute;
    width: 10rem;
    right: 6rem;
    bottom: -5.3rem;
    display: flex;
    justify-content: space-between;
  }
  &__btn {
    padding: 1rem;
  }
}

.color1 {
  background-color: #0d95e8;
}
.color2 {
  background-color: #0b2e13;
}
.color3 {
  background-color: #00c2b4;
}
.color4 {
  background-color: #1c7430;
}
.color5 {
  background-color: #5c4ceb;
}