@import './utils/index.scss';
@import './components/index.scss';
@import './layout/index.scss';
@import './overall.scss';

* {
  box-sizing: border-box;
}

html, body {
  font-size: 10px;
  font-family: $mainFont;
  font-weight: normal;
  background-color: $background1;
}

//Скрывает основную страницу сверху
// .mainPage
//   margin-top: -1000rem

// .container
//   width: calc(100vw - 60%)
//   max-width: 100vw
// margin: 0 auto



//-------------------------------------
// Скрывает слайдер с маленьких девайсов
.swiper-container {
  display: none;
  @media ($tablet) {
    display: flex;
  }
}

.sliderPreviewSites__title {
  display: none;
  @media ($tablet) {
    display: flex;
  }
}

.sliderPreviewSites__navigation {
  display: none;
  @media ($tablet) {
    display: flex;
  }
}

//-------------------------------------


.activeBlock {
  display: block;
}

.hideBlock {
  display: none;
}

.container-nav {
  width: 100%;
  max-width: 125rem;
  margin: 0 auto;
}

.activeMenu {
  font-weight: 500;
  color: $colorLinksActive;
  // text-decoration: underline;
}

// Показать/скрыть блок Интерактив
.showInteractive {
  height: auto;
}

.opacityShow {
  opacity: 1;
}

// КРАСИМ
// Красим ссылки навигации основного меню


// Красим бордер рамки у кнопки скрыть
.htmlBtn {
  border: 1.5px solid $red2;
}
.jsBtn {
  border: 1.5px solid $blue2;
}
.cmsBtn {
  border: 1.5px solid #53c0dd;
}

// Красим бэкграунд у гамбургера
.htmlHamburger {
  background-color: $red2;
}
.jsHamburger {
  background-color: $blue2;
}
.cmsHamburger {
  background-color: #53c0dd;
}

// COLORS
.bold {
  font-weight: 700;
}

