// Появление основного меню
.showMenu {
   animation: showMenu 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.hideMenu {
   animation: hideMenu 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
   
.opacityShow {
   animation-name: opacityShow;
   animation-duration: 0.3s;
}
   
.rotate-90-ccw {
   animation: rotate-90-ccw 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
   
.rotate-anti-90 {
   animation: rotate-anti-90 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}


// Keyframes

// Всплытие Меню
@keyframes showMenu {
   0% {
      transform: scaleY(0.4);
      transform-origin: 100% 0%;
   }
   100% {
      transform: scaleY(1);
      transform-origin: 100% 0%;
   }
}

@keyframes hideMenu {
   0% {
      transform: scaleY(1);
      transform-origin: 100% 0%;
   }
   100% {
      transform: scaleY(0);
      transform-origin: 100% 0%;
   }
}

@keyframes opacityShow {
   0% {
      opacity: 0;
   }
   70% {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}


// Кнопка-крестик
@keyframes rotate-90-ccw {
   0% {
      transform: rotate(0);
   }
   100% {
      transform: rotate(-45deg) translate(2.5px, -2.5px);
   }
}

@keyframes rotate-anti-90 {
   0% {
      transform: rotate(-45deg) translate(2.5px, -2.5px);
   }
   100% {
      transform: rotate(0)
   }
}